import React from 'react'
import { useLocation , Route,Routes} from 'react-router-dom'
import { AnimatePresence } from 'framer-motion';
import Home from './Home';
import Aboutus from './Aboutus';
import OurTeam from './OurTeam';
import YourProfile from './YourProfile';
import LogInPage from './LogInPage';
import Services from './Services';
function AnimateRoutes() {
  const location = useLocation();

  return (
    <div>
        <AnimatePresence key={location.pathname}>
        <Routes>
          <Route path='/' element={<Home/>}/>
          <Route path='/aboutus' element={<Aboutus/>}/>
          <Route path='/services' element={<Services/>}/>
          <Route path='/services/:javafullstack' element={<Services/>}/>
          <Route path='/services/:mernstack' element={<Services/>}/>
          <Route path='/services/:data-analysis' element={<Services/>}/>
          <Route path='/services/:meanstack' element={<Services/>}/>
          <Route path='/services/:machinelearning' element={<Services/>}/>
          <Route path='/services/:pythonstack' element={<Services/>}/>
          <Route path='/ourteam' element={<OurTeam/>}/>
          <Route path='/yourprofile' element={<YourProfile/>}/>
        </Routes>
        </AnimatePresence>
    </div>
  )
}

export default AnimateRoutes