        serviceSub2:['IOS App Development','https://img.freepik.com/premium-photo/ui-ux-representations-with-smartphone_974729-79087.jpg?w=1060','With 5+ Years Of Hands-On IOS Development Experience, We Are Trusted By Clients Ranging From Startups To Established Firms And Non-Profit Organizations. Our Highly-Skilled Team Of 250+ Business Analysts, Developers, Designers, And QA Engineers Turns Your Ideas Into Winning Solutions That Give You A Competitive Edge On The Market. Full-Cycle IOS Development Services Our Company Provides'],
        serviceSub3:['Hybrid App Development','https://img.freepik.com/free-vector/gradient-ui-ux-background_23-2149065783.jpg?t=st=1718732085~exp=1718735685~hmac=b36b03c232895d02b3c38f80996b1909d9c2b92455b15fd8418caec16176d16c&w=1060','Our Hybrid App Development services at ZeeTee Technologies offer the perfect blend of native and web applications, delivering versatile and high-performing solutions for your business. Leveraging the latest frameworks like React Native and Flutter, our skilled developers create apps that run smoothly across both iOS and Android platforms, ensuring a consistent user experience. We focus on crafting feature-rich, scalable, and cost-effective hybrid apps that meet your specific requirements, reduce time-to-market, and maximize reach. Whether you need a robust e-commerce platform, a dynamic social networking app, or a complex enterprise solution, our team ensures your app is optimized for performance, security, and user engagement. Partner with us to bring your vision to life with innovative hybrid app solutions that drive success in today’s competitive landscape.'],
        serviceSub4:['Web App Development','https://img.freepik.com/free-vector/app-development-illustration_52683-47931.jpg?t=st=1718732185~exp=1718735785~hmac=c5c3b8e00952217179e355de6c1bfc6631f3193fe6952688f4c0b757e3cd9d8a&w=1060','At ZeeTee Technologies, our Web App Development services are designed to create cutting-edge, secure, and scalable web applications that drive business growth and enhance user engagement. With over 5 years of industry experience, our expert developers leverage the latest technologies and best practices to deliver custom solutions tailored to your unique business needs. We excel in crafting intuitive and high-performing web applications, whether you re starting from scratch or looking to upgrade an existing platform. Our comprehensive services include e-commerce integration, custom web development, and seamless user experiences, ensuring your online presence is robust and impactful. Trust Alpine Technologies to transform your business requirements into reliable, future-proof web applications that deliver measurable results.'],
