        serviceUpDesc:'Are You Looking For An ECommerce Website Development Company To Build Your Online Shop? Set Up Your Online Store Now To Sell Your Products Online & Increase Your Sales. ZeeTee Technology Helps You Build An Impressive ECommerce Website. Have Something In Your Mind & Wanna Build Your ECommerce Website? Share Your Requirements And Leave It On Us To Develop What You Are Looking For.',
        serviceUpperDesc:['https://www.alpinetechnologies.in/assets/img/pages/seoand-smo.png','Our E-Commerce Development Services are tailored to transform your business into a thriving online store. We specialize in creating user-friendly, secure, and scalable e-commerce platforms that enhance the shopping experience and drive sales. Our services include custom website design, mobile optimization, payment gateway integration, product management, and robust backend support. We employ the latest technologies and best practices to ensure your e-commerce site is not only visually appealing but also optimized for performance, security, and SEO. From startups to established enterprises, our solutions are designed to meet the unique needs of your business, ensuring seamless operations and a superior customer experience.'],
        serviceSub1:['Opencart Development','https://img.freepik.com/free-vector/customer-with-shopping-cart-buying-digital-service-online-digital-service-marketplace-ready-digital-solution-online-marketplace-framework-concept_335657-894.jpg?t=st=1718776074~exp=1718779674~hmac=16a67fd5a7a979ecf32eba31c8fcc91035cc6a428961a3db4db071b6da127921&w=1060','At ZeeTee We Offer OpenCart Web Development Services To Help Customize Your Store As Per Your Business Needs Thereby Expanding Your Reach To A Global Audience. By Leveraging The Scalability, Store Management Capabilities, Readily Available Modules And Themes, We Ensure That Your ECommerce Store Stands Out From The Crowd. Irrespective Of The Complexity Of The Requirement, Our Experienced OpenCart Developers Can Customize Your ECommerce Store To Reflect The True Essence Of Your Brand.'],
        serviceSub2:['Woocommerce Development','https://img.freepik.com/free-vector/online-shopping-concept_52683-63898.jpg?t=st=1718776135~exp=1718779735~hmac=43a956703b693797d908bdad5b2a662f507ad2b6d2c1a1a94506885de8ac2e73&w=1060','Our Ecommerce Development Services Include Building, Customizing And Implementing WooCommerce Development Services For Diverse Industries. Be It A WooCommerce Plugin Development For Your Designing A Theme According To Your Business Needs, We Have It All. Our Team Offers A 100% Client Satisfaction With WooCommerce Store Development Services Ranging From Designing, Development To Payment Integrations Catering Your Needs. With Developers Having Years Of Experience With WooCommerce Store, We Provide Customized User Experience And Help Your Store Stand Out Against Your Competitors.'],
        serviceSub3:['Magento Development','https://img.freepik.com/free-vector/ecommerce-internet-shopping-promotion-campaign_335657-2977.jpg?t=st=1718776212~exp=1718779812~hmac=c5487b7d1fed93cc9958c1953c495e3deabb9749dff2d8277fc17216baa1dc08&w=1060','ZeeTee Is A Leading Magento Development Company Where You Can Find Magento Skills At An Expert Level. We Strive To Provide Robust Storefront Models To Our Clients By Working With Various Magento Versions Like Magento 1.9, Magento 2.0. We Have A Team Of Magento Certified Developers With Over 6 Years Of Experience With A Specialization In The Ecommerce Orientation At The Web And Therefore We Also Offer Custom Magento Web Designs.'],
        serviceSub4:['Custom Web Development','https://www.alpinetechnologies.in/assets/img/pages/adwords.png','We Help Our Clients To Scale Higher Levels Of Performance By Developing Innovative Application Development Services That Address Their Business Objectives, Reduce Total Cost Of Ownership And Increase Value From IT Investments. Our In-Depth Industry Knowledge, Technical Expertise, Robust Processes, Platforming Approach, Global Delivery Model, And Expertise At Optimizing IT Environments In Globally Scalable Environments Help Us Deliver Measurable Business Results And Transform Our Customers Their Application Development Investments Into Reliable And Future-Proof Applications.'],
